import React, {Component} from 'react';
import {Card} from "antd";
import data from "./data";

export default class Book extends Component {
    render() {
        return (
            <div className="wrap">
                {
                    data.map((item,index)=>(
                        <Card
                            title={item.title}
                            loading={false}
                            type="inner"
                            key={index}
                            className="card"
                        >
                            <div
                                dangerouslySetInnerHTML={{
                                    __html:item.content
                                }}
                            >
                            </div>
                        </Card>
                        )
                    )
                }
            </div>
        );
    }
}