import React, {Component} from 'react';
import {Tag} from "antd";

const isTag={
    top:{
        color:"volcano",
        text:"置顶"
    },
    good:{
        color:"geekblue",
        text:"精华"
    },
    share:{
        color:"green",
        text:"分享"
    },
    ask:{
        color:"magenta",
        text:"问题"
    },
    job:{
        color:"cyan",
        text:"招聘"
    },
    dev:{
        color:"lime",
        text:"测试"
    }
};
function getTap(data) {
    return data.top?"top":
                data.good?"good":data.tab;

}

export default class MyTag extends Component {
    render() {
        const tag=isTag[getTap(this.props.data)];
        return tag?(
            <Tag color={tag.color} >{tag.text}</Tag>
        ):"";
    }
}