import React, {Component} from 'react';
import {connect} from "react-redux";
import {Avatar,Row,Col,Skeleton} from "antd";
import UserList from "../../components/user/list";
import {getUser} from "../../redux/actions";
//import data from "./data";
import "./index.css";

class User extends Component {
    constructor(arg){
        super(arg);
        console.log(arg.match.params.id)
        arg.getUser(arg.match.params.id);
    }
    shouldComponentUpdate(nexprops,nextstate){
        if(this.props.match.params.id!==nexprops.match.params.id){
            nexprops.getUser(nexprops.match.params.id);
            return false;
        }
        return true;
    }
    render() {
        //console.log(this.props)
        const data=this.props.state.user;
        //console.log(data)
        const {loginname,avatar_url,create_at,score,recent_topics,recent_replies}=data.data;
        return data.data?(
            <div className="wrap">
                <Avatar src={avatar_url} className="userAvatar" />
                <Row className="userInfo">
                    <Col md={8} xs={24}>
                        用户名:<a href="###">{loginname}</a>
                    </Col>
                    <Col md={8} xs={24}>
                        积分:<a href="###">{score}</a>
                    </Col>
                    <Col md={8} xs={24}>
                        注册日期:<a href="###">{create_at.split("T")[0]}</a>
                    </Col>
                </Row>
               <UserList title="最近发布过的话题" loading={false} data={recent_topics}/>
               <UserList title="回复的文章" loading={false} data={recent_replies}/>
            </div>
        ):( <div className="wrap">
                <Skeleton avatar paragraph={{ rows: 4 }} />
            </div>);
    }
}
export default connect(
    state=>({state:state}),
    {getUser}
)(User)