import React, {Component} from 'react';
import {connect} from "react-redux";
import Content from "../../components/details/content";
import Comments from "../../components/details/comments";
import {getDetalis} from "../../redux/actions";
//import data from "./data";

class Details extends Component {
    constructor(arg){
        super(arg);
        //console.log(arg.id)
        arg.getDetalis(arg.match.params.id)
    }
    shouldComponentUpdate(nextprops,nextstate){
        if(this.props.match.params.id!==nextprops.match.params.id){
            nextprops.getDetalis(nextprops.match.params.id)
            return false;
        }
        return  true;
    }
    render() {
        //console.log(this.props.match.params.id);
        //console.log(this.props.state)
        const {data,loading}=this.props.state.details;
        return (
            <div className="wrap">
                <Content  data={data} loading={loading} />
                <Comments data={data.replies} count={data.reply_count} loading={false}/>
            </div>
        );
    }
}
export default connect(
    state=>({state:state}),
    {getDetalis}
)(Details)