import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {Menu} from "antd";
import {ProjectOutlined,HomeOutlined,SafetyCertificateOutlined } from"@ant-design/icons";

const {Item}=Menu;
export default class Nav extends Component {
    state={
        selectedKey:window.localStorage.getItem("key")?window.localStorage.getItem("key"):"1"
    }
    selectKey=(e)=>{

        window.localStorage.setItem("key",e.key);
        this.setState({
            selectedKey:e.key
        })
    }
    render() {
        //console.log(this.props);
        const{mode,id}=this.props;
        return (
            <Menu
                mode={mode}
                id={id}
                selectedKeys={[this.state.selectedKey]}
                onClick={this.selectKey}
            >
                <Item key="1">
                    <Link to="/index"><HomeOutlined/>首页</Link>
                </Item>
                <Item key="2">
                    <Link to="/book"><ProjectOutlined />教程</Link>
                </Item>
                <Item key="3">
                    <Link to="/about"><SafetyCertificateOutlined/>关于</Link>
                </Item>
            </Menu>
        );
    }
}