import axios from "axios";
import {GETDATA,GETDETALIS,GETUSER,GETCOUNT} from "./actions-types";

export const getCount=(tab)=>(dispatch=>{
        axios.get(`https://cnodejs.org/api/v1/topics?tab=${tab}`)
            .then(res=>{
                //console.log(res.data.data);
                const {data,success}=res.data;
                if(success){
                    //console.log(data)
                   dispatch(putCount(data.length))
                }
            })
            .catch(err=>{
                console.log(err)
            })
    }
)
export const putCount=(count)=>({type:GETCOUNT,data:count})


export const getData=(tab,page,limit)=>((dispatch)=>{
    axios.get(`https://cnodejs.org/api/v1/topics?tab=${tab}&page=${page}&limit=${limit}`)
        .then(res=>{
            //console.log(res.data.data);
            const {data,success}=res.data;
            if(success){
                //console.log(data)
                dispatch(putData(data))

            }
        })
        .catch(err=>{
            console.log(err)
        })
});

export const putData=(data)=>({type:GETDATA,data:data});

export const getDetalis=(id)=>((dispatch)=>{
    axios.get(`https://cnodejs.org/api/v1/topic/${id}`)
        .then(res=>{
            //console.log(res.data.data)
            dispatch(putDetalis(res.data.data))
        })
        .catch(err=>{
            console.log(err);
        })
})
export const putDetalis=(data)=>({type:GETDETALIS,data})

export const getUser=(loginname)=>((dispatch)=>{
    axios.get(`https://cnodejs.org/api/v1/user/${loginname}`)
        .then(res=>{
            //console.log(res.data.data)
            dispatch(putUser(res.data.data))
        })
        .catch(err=>{
            console.log(err);
        })
})
export const putUser=(data)=>({type:GETUSER,data})