import React, {Component} from 'react';
import {Card,Avatar,List} from "antd";
import {Link} from  "react-router-dom";

export default class Comments extends Component {
    render() {
        const {data,count=0,loading}=this.props

        return (
            <Card
                loading={loading}
                title={`${count}条回复`}
                type="inner"
            >
                <List
                    itemLayout="vertical"
                    dataSource={data}
                    renderItem={(item)=>(
                        <List.Item
                            key={item.id}
                            extra={item.ups.length>0?<span>有{item.ups.length}人觉得这条回复很赞</span>:""}
                        >
                            <List.Item.Meta
                                avatar={<Avatar src={item.author.avatar_url}/>}
                                description={
                                    <div>
                                        <Link to={`/user/${item.author.loginname}`}>{item.author.loginname}</Link>
                                        <span>发布于:{item.create_at.split("T")[0]}</span>
                                    </div>
                                }
                            >
                            </List.Item.Meta>
                            <div dangerouslySetInnerHTML={{__html:item.content}}>

                            </div>
                        </List.Item>
                        )}
                />

            </Card>
        );
    }
}