import {GETDATA,GETCOUNT} from "../actions-types";
export function list(state={
                        loading:true,
                        data:[],
                        count:0
                     },action) {
    switch (action.type){
        case GETCOUNT:
            return {
                ...state,
                count:action.data
            };
        case GETDATA:
            return {
                ...state,
                loading:false,
                data:action.data
            };
        default:
            return state;
    }
}