import {GETDETALIS} from "../actions-types";
export function details(state={
                            loading:true,
                           data:""
                        },action) {
    switch (action.type){
        case GETDETALIS:
            return {
                loading:false,
                data:action.data
            };
        default:
            return state;
    }
}
