import React, {Component} from 'react';
import {connect} from "react-redux";
import {List,Avatar,Pagination } from "antd";
import {Link} from "react-router-dom";
import MyTag from "../tag/tag";
import {getData,getCount} from "../../redux/actions";
//import data from "./data";

 class MyList extends Component {
     constructor(arg){
         super(arg);
         this.state={
             page:1
         }
        // console.log(arg.tab)
        arg.getCount(arg.tab);
        arg.getData(arg.tab,this.state.page,15);
     }
    // componentWillMount(){
    //     this.props.getCount(this.props.tab);
    // }
     shouldComponentUpdate(newprops,nextstate){
         if(this.props.tab !== newprops.tab){
             newprops.getData(newprops.tab,this.state.page,15);
             newprops.getCount(newprops.tab);
             return false;
         }
         if(this.state.page!==nextstate.page){
             newprops.getData(newprops.tab,nextstate.page,15);
             return false;
         }
         return true;
     }
     onChange=(e)=>{
         //console.log(e)
         this.setState({
             page:e
         })
     }
    render() {
        //console.log(this.props);
        const {loading,data,count}=this.props.state;
        //console.log(count);
        return (
            <>
            <List
                loading={loading}
                dataSource={data}
                renderItem={
                    item=>(<List.Item
                        actions={[`回复:${item.reply_count}`,`访问:${item.visit_count}`]}
                    >
                        <List.Item.Meta
                            avatar={<Avatar src={item.author.avatar_url} />}
                            title={<div>
                                <MyTag data={item}/>
                                <Link to={`/details/${item.id}`}>{item.title}</Link>
                            </div>}
                            description={<p style={{margin:0}}>
                                <Link to={`/user/${item.author.loginname}`} >{item.author.loginname}</Link>
                                发布于:{item.create_at.split("T")[0]}
                            </p>}
                        />
                    </List.Item>)
                }
                id="MyList"
            >
            </List>
            <Pagination current={this.state.page} onChange={this.onChange} total={count} pageSize="15" />
            </>
        );
    }
}
export default connect(
    state=>({state:state.list}),
    {getData,getCount}
)(MyList)