import React, {Component} from 'react';
import {Row,Col} from "antd";
import MyMenu from "../../components/index/menu";
import MyList from "../../components/index/list";
import "./index.css";

export default class Index extends Component {
    render() {
        //console.log(this.props);
        const {tab}=this.props.match.params;
        return (
            <Row className="content-main">
                <Col md={5} xs={0}>
                    <MyMenu className="main-menu" mode="vertical" tab={tab}/>
                </Col>
                <Col md={0} xs={24}>
                    <MyMenu className="xs-main-menu" mode="horizontal" tab={tab}/>
                </Col>
                <Col md={19} xs={24} className="list">
                    <MyList tab={tab} />
                </Col>
            </Row>
        );
    }
}